@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital@1&display=swap);
body {
  margin: 10px;
  background-color: azure;
  
}

.card-container{
  display: grid;
   grid-template-columns: repeat(3, 1fr);
}


.form-row{
  width: 38rem;
  margin-left: 20px;
  margin-right: 200px;
}
.form-r{ 
  margin-left: 20px;
  margin-right: 20px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-left: 20px;
  padding-top: 20px;
  padding-right: 20px;
  
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-left: 20px;
  padding-top: 20px;
  padding-right: 20px;
  background-color: azure;
}

.balance {
  color: black;
  font-family: "Anton", sans-serif;
  font-size: x-large;
  list-style: none;
  text-decoration: none;
}

.brand-name {
  color: black;
  font-family: "Anton", sans-serif;
  font-size: large;
 
}

.btn{
  margin-left: 8px;
}

.tm{
  color: white;
}

.card{
  width: 37rem;
  margin: 20px;
}


.card-title{
  font-family: "Libre Baskerville:ital@1", sans-serif;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

